export default {
  data() {
    return {
      magazineList: null,
      tagList: [],
      tagsSelected: null,
      error: null,
    }
  },
  async mounted() {
    try {
      this.magazineList = await this.$strapi.find('magazines')
      this.tagList = await this.$strapi.find('tags')
    } catch (error) {
      this.error = error
    }
  },
}
